.page-header {
  display: flex;
  flex-direction: column;
  margin: 0;
  width: 100%;
  min-height: 1072px;

  font-family: $font-family;
  font-size: 15px;
  font-weight: 400;
  line-height: 29px;
  color: $white-color;

  background-color: $bcg-color-blue;
  background-image: url("../img/bg-hero.jpg");
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: cover; 
}


@media (min-width: $desktop-width) {
  .page-header__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;    
    height: 100%;

    padding-right: 155px;
    padding-left: 155px; 
  }

  .page-header__logo-wrapper {
    align-self: center;
    margin-top: 285px;
    margin-bottom: 85px;
  }

  .page-header__logo {
    display: block;
  }

  .page-header__title {
    @include margin0-padding0;
    margin-bottom: 35px;

    font-size: 54px;
    font-weight: 700;
    line-height: 60px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 8px;
  }

  .page-header__text {
    font-size: 15px;
    font-weight: 400;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .page-header__scroll-wrapper {
    margin-top: 235px;
    align-self: center;
  }

  .page-header__scroll {
    display: block;

    &:hover {
      width: 26px;
      height: auto;
    }
  }
}


