@import "variables.scss";
@import "mixins.scss";

@import "blocks/page-header.scss";
@import "blocks/about-us.scss";
@import "blocks/services.scss";
@import "blocks/whatwedo.scss";
@import "blocks/smth.scss";
@import "blocks/contacts.scss";
@import "blocks/page-footer.scss";

@import "blocks/page-main.scss";



body {
  @include margin0-padding0;
  min-width: 320px;

  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  color: $text-color;
  background-color: $white-color;
  overflow-x: hidden;
}

img {
  // max-width: 100%;
  height: auto;
}

p, ul {
	@include margin0-padding0;
}

a {
  text-decoration: none;
}


.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}


/* lato-300 - latin */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/lato-v15-latin-300.eot'); /* IE9 Compat Modes */
  src: local('Lato Light'), local('Lato-Light'),
       url('../fonts/lato-v15-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/lato-v15-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/lato-v15-latin-300.woff') format('woff'), /* Modern Browsers */
       url('../fonts/lato-v15-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/lato-v15-latin-300.svg#Lato') format('svg'); /* Legacy iOS */
  font-display: optional;
}
/* lato-regular - latin */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/lato-v15-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Lato Regular'), local('Lato-Regular'),
       url('../fonts/lato-v15-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/lato-v15-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/lato-v15-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/lato-v15-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/lato-v15-latin-regular.svg#Lato') format('svg'); /* Legacy iOS */
  font-display: optional;
}
/* lato-700 - latin */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/lato-v15-latin-700.eot'); /* IE9 Compat Modes */
  src: local('Lato Bold'), local('Lato-Bold'),
       url('../fonts/lato-v15-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/lato-v15-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
