}

.container {
  box-sizing: border-box;
  width: 100%;
  
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}


@media (min-width: $tablet-width) {
	.container {    
    max-width: 900px;
  }
}

@media (min-width: $desktop-width) {
  .container {    
    padding-right: 0;
    padding-left: 0;
  }
}
