.smth {
  width: 100%;
}


@media (min-width: $desktop-width) {
  .smth__wrapper {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -10px;
  }


  .smth__item {
    height: 237px;
    margin-left: 10px;
    margin-bottom: 10px;    
  }

  .smth__item-link {
    position: relative;
    display: block;
    height: 100%;    
  }

  

  .smth__picture {
    height: 100%;
    overflow: hidden;
  }

  .smth__image {
    display: block;
    width: 100%;
    height: 100%;
  }

  .smth__image-after  {
    position: absolute;
    display: none;

    left: 0;
    top: 0;

    width: 100%;
    height: 100%;
  }

  .smth__item-link:hover .smth__image-after {
    display: block;
    background-color: $black-color-20;
  }

  .smth__title {
    position: absolute;

    bottom: 37px;
    left: 42px;

    @include margin0-padding0;

    font-size: 34px;
    
    color: $white-color;
    text-transform: uppercase;
  }

  .fg1 {
    flex-basis: 28.1%;
    flex-grow: 1;
  }

  .fg2 {
    flex-basis: 41.4%;
    flex-grow: 2;
  }
}
