.whatwedo {
  padding: 119px 0;
  
  background-color: $white-color;
}

@media (min-width: $desktop-width) {
  .whatwedo__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 0 105px;
    
    border-right: 2px solid $text-color;
    border-left: 2px solid $text-color;
    border-bottom: 2px solid $text-color;

    &:before {
      position: absolute;
      content: "";

      top: 0;
      left: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }

    &:after {
      position: absolute;
      content: "";

      top: 0;
      right: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }
  }

  .whatwedo__title {
    @include margin0-padding0;

    margin-top: -23px;
    margin-bottom: 107px;
    padding: 0 100px;


    font-size: 34px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 6px;
  }

  .whatwedo__item {
    display: flex;
    flex-direction: column;
    
    margin-bottom: 97px;
  }

  .whatwedo__item:last-child {
    margin-bottom: 85px;
  }

  .whatwedo__icon-wrapper {
    align-self: center;
    margin-bottom: 33px;
  }

  .whatwedo__subtitle {
    @include margin0-padding0;

    margin-bottom: 25px;

    font-size: 20px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 3px;
  }

  .whatwedo__text {
    font-size: 14px;
    font-weight: 300;
    line-height: 26px;
    text-align: center;
    white-space: pre-wrap;
  }
}