.contacts {
  padding: 160px 0 50px;
  
  background-color: $white-color;
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 0 125px;
    
    border-right: 2px solid $text-color;
    border-left: 2px solid $text-color;
    border-bottom: 2px solid $text-color;

    &:before {
      position: absolute;
      content: "";

      top: 0;
      left: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }

    &:after {
      position: absolute;
      content: "";

      top: 0;
      right: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }
  }

  .contacts__title {
    @include margin0-padding0;

    margin-top: -19px;
    margin-bottom: 55px;
    padding: 0 100px;


    font-size: 30px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 6px;
  }

  .contacts__text {
    margin-bottom: 40px;

    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    text-align: center;
    white-space: pre-wrap;
  }

  .contacts__btn-wrapper {
    box-sizing: border-box;
    margin-bottom: 48px;
  }

  .contacts__btn {
    box-sizing: border-box;
    display: inline-block;
    
    padding: 20px 135px;
    font-size: 16px;
    font-weight: 400;

    text-align: center;
    text-transform: uppercase;
    letter-spacing: 3px;

    color: $white-color;
    background-color: $text-color;

    &:hover {
      background-color: #000;
    }
  }
}