.about-us {
  padding: 160px 0;
  
  background-color: $white-color;
}

@media (min-width: $desktop-width) {
  .about-us__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 0 185px;
    
    border-right: 2px solid $text-color;
    border-left: 2px solid $text-color;
    border-bottom: 2px solid $text-color;

    &:before {
      position: absolute;
      content: "";

      top: 0;
      left: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }

    &:after {
      position: absolute;
      content: "";

      top: 0;
      right: 0;

      width: 27%;
      height: 2px;

      background-color: $text-color;
    }
  }

  .about-us__title {
    @include margin0-padding0;

    margin-top: -23px;
    margin-bottom: 50px;
    padding: 0 100px;


    font-size: 34px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 6px;
  }

  .about-us__text {
    margin-bottom: 25px;

    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    text-align: center;
  }

  .about-us__text:last-of-type {
    padding: 0 45px;
  }
}