.services {
  padding: 126px 0 108px;  
  
  background-color: $bcg-color-blue;
  background-image: url("../img/services.jpg");
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: cover;
}

@media (min-width: $desktop-width) {
  .services__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 0 185px;

    border-right: 2px solid $white-color;
    border-left: 2px solid $white-color;
    border-bottom: 2px solid $white-color;

    &:before {
      position: absolute;
      content: "";

      top: 0;
      left: 0;

      width: 27%;
      height: 2px;

      background-color: $white-color;
    }

    &:after {
      position: absolute;
      content: "";

      top: 0;
      right: 0;

      width: 27%;
      height: 2px;

      background-color: $white-color;
    }
  }

  .services__title {
    @include margin0-padding0;

    margin-top: -23px;
    margin-bottom: 50px;
    padding: 0 100px;


    font-size: 34px;
    font-weight: 700;
    color: $white-color;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 6px;
  }

  .services__text {
    margin-bottom: 25px;

    font-size: 16px;
    font-weight: 300;
    line-height: 26px;
    color: $white-color;
    text-align: center;
  }

  .services__text:first-of-type {
    margin-bottom: 0;
  }
}